$main-color-dark: #160101;
$main-color: #301616;
$main-color-light: #4D3D3D;

@import 'header';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: white;
  text-decoration: none;
}

button {
  border: none;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  outline: none;
  //margin-top: 50px;
}

p {
  width: 70%;
  margin: 10px auto 20px;
}

#root {
  font-size: 18px;
  max-width: 100%;
  width: 100vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(180deg, $main-color-dark, $main-color);
  color: white;
  min-height: 100vh;
  padding-bottom: 200px;
}

#root > .app {
  width: 1000px;
  overflow-x: hidden;
}

.app-contents {
  transition: 0.5s;
  display: flex;
  width: 2040px;

  > .content-pane {
    align-items: center;
    box-sizing: border-box;
    margin: 10px;
    width: 990px;
  }
}

.section-title {

  border-bottom: 1px solid white;
  margin-top: 40px;
  margin-bottom: 20px;

  > .title {
    font-size: 2em;
    font-weight: bold;
    line-height: 0.75;
  }

  > .subtitle {
    line-height: 1.3;
    font-size: 1.25em;
    color: rgba(255, 255, 255, 0.8)
  }
}

.sub-section-title {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding-bottom: 5px;
  width: 30%;
  margin-left: 10px;

  > .title {
    font-size: 1.5em;
    font-weight: bold;
    line-height: 1.2;

  }
}

.section-contents {
  display: flex;
  justify-content: center;

  &.projects {
    flex-wrap: wrap;
  }

  &.about {

    > div {
      flex: 1;
    }

    > .description {
      box-sizing: border-box;
      padding-right: 20px;
      font-size: 1.25em;

      > div {
        margin-bottom: 20px;
      }
    }

    > .other-junk {
      background-color: $main-color;
      border-radius: 20px;
      overflow: hidden;

      display: flex;
      flex-direction: column;
      align-items: center;
      border: 1px solid white;

      > img {
        max-width: 100%;
        height: auto;
      }

      > button {
        width: 100%;
        border-radius: 0;
        height: 60px;
      }
    }
  }
}

.project-card {
  border: 1px solid white;
  overflow: hidden;
  border-radius: 10px;
  margin: 5px;
  width: 480px;
  height: 270px;
  cursor: pointer;

  &:hover {
    > .title {
      transform: translate(0, -50px);
    }
  }

  > .img-holder {
    width: 100%;

    > img {
      width: 100%;
    }
  }

  > .title {
    transition: 0.2s;
    padding: 7px;
    text-align: center;
    box-sizing: border-box;
    height: 50px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 1.2em;
    background-color: $main-color-dark;
  }
}

.btn {
  background: radial-gradient(farthest-corner at 70px 20px, $main-color-light 0%, $main-color 30%) 0 60%;
  background-size: 200% 200%;
  font-size: 1.2em;
  border-radius: 5px;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s;
  height: 50px;
  cursor: pointer;

  &:hover {
    background-position: 0 40%;
  }

  &:focus {
    transform: scale(0.9);
    background-position: 0 80%;
  }
}

.button-section {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  margin: auto;
}

ul {
  display: flex;
  padding-left: 0;
  list-style-type: none;
  flex-wrap: wrap;

  li {
    background-color: $main-color;
    min-width: 150px;
    border: 1px solid white;
    border-radius: 5px;
    padding: 2px;
    text-align: center;
    height: 25px;
    margin: 10px;
  }
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  //> .img-holder {
  //  width: 240px;
  //  height: 135px;
  //  margin: 10px;
  //  border-radius: 5px;
  //border: 1px solid white;
  //  width: 240px;
  //  height: 135px;
  //  margin: 10px;

  > .img-space {
    width: 240px;
    height: 135px;
    margin: 10px;
    border-radius: 5px;
    border: 1px solid white;
    background-color: black;
  }


  > img {
    width: 240px;
    height: 135px;
    margin: 10px;
    //position: absolute;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid white;
    transition: 0.2s;
    top: initial;
    //z-index: 2;

    &.enlarged {
      position: absolute;
      transform: scale(3); //translate(-50%,-50%);
      //top: 40%;
      //left: 69%;
      z-index: 2;
    }

    //}
  }
}
