
.header {
  width: 100%;
  padding: 0 50px 0;
  margin-bottom: 30px;
  box-sizing: border-box;
  min-height: 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;

  > img {
    height: 80px;
    width: auto;
    margin-bottom: 10px;
  }


  > .nav-bar {
    display: flex;
    height: inherit;
    align-items: center;

    > .nav-item, > .btn {
      cursor: pointer;
      flex: 1;
      height: 100%;
      min-width: 150px;
      margin: 10px;
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      transition: 0.2s;
      background-color: inherit;

      &:hover {
        background-color: rgba(255, 255, 255, 0.2);
      }

      > .title {
        font-size: 1.8em;
        font-weight: bold;
        line-height: 0.75;
      }

      > .subtitle {
        height: 15px;
        font-size: 1.2em;
        color: rgba(255, 255, 255, 0.8)
      }
    }
  }
}

@media (max-width: 1100px) {
  .header {
    flex-direction: column;

    > .nav-bar {
      flex-wrap: wrap;
    }
  }
}
